<template>
    <div>
        <!-- 阅读界面 -->
<div class="all" >
       <transition name="show_top" >
        <div class="top" v-show="isshow">
        
            <div class="prev">
                <span @click="prev"></span>
            </div>
             <div class="title">
                <p>第{{$store.state.books.read_id}}话</p>
            </div>
           </div>
             </transition>
              <transition name="show_footer" >
           <div class="footer" v-show="isshow">
              <div class="jump" ref="jump" >
                   <span class="prev_chapter" @click="prev_chapter"></span>
                    <span class="next_chapter" @click="next_chapter"></span>
              </div>
           </div>
            </transition>
    </div>
    <div class="scroll" @click="show">
        <img  v-for="(item,index) in $store.state.books.chapter[$store.state.books.read_id-1]" :src="item.src" :key="index" alt="">
    </div>
        <p class="tip">本话已结束.......</p>
    </div>
</template>
<script>
import Vue from 'vue';
import { Toast } from 'vant';
import { Dialog } from 'vant';
Vue.use(Dialog);
Vue.use(Toast);
export default {
    data(){
        return{
            isshow:false,
            timer:null,
            read_id:1,
        }
    },
    watch:{
        "$store.state.books.read_fina":function(newval){
            if(newval==true){
                Toast("已经是最后一话了");
                this.$store.commit("books/final");

            }
        },
        "$store.state.books.read_first":function(newval){
            if(newval==true){
                Toast("已经是第一话了");
                 this.$store.commit("books/start");
            }
        },
       
    },
        methods:{
      
            show(){
                if(this.isshow==true){
                    this.isshow=false
                }else{
                    this.isshow=true;
                    clearInterval(this.timer);  
                    let _this=this;
                      this.timer=setTimeout(function(){
                        _this.isshow=false;
                        
                    }, 2000 );   
                }
            },
        prev(){
            this.$store.commit('books/init_chapter');
            this.$router.go(-1);
        },
        next_chapter(){
            if(this.$store.state.user.user[this.$store.state.user.userID].coupons>=10){
                this.$store.commit('books/next_chapter');
                this.$store.commit('user/next');
            }else{
                Dialog.confirm({
                    title: '提示',
                    message: '您的余额不足，是否去充值'
                    }).then(() => {
                    this.$router.push({path:"/payment"})
                    }).catch(() => {
                    });
            }
            

        },
        prev_chapter(){

            this.$store.commit('books/prev_chapter');
             this.$store.commit('user/prev');

        }
        
        
    },
     beforeDestroy(){
       this.$store.commit('user/init');
    },
}
</script>
<style lang="scss" scoped>
    img{
        display:block;
        width:100%;
    }
.footer{
    position:fixed;
    left:0;
    right:0;
    bottom:0;
    background-color: rgba(40, 40, 40, 0.95);
    display:flex;
    height:2.5rem;
    z-index:2;
    .jump{
        width:6.4rem;
        display:flex;
        margin:0 auto;
        .prev_chapter{
        display:block;
        width:1.35rem;
        height:1.35rem;
        background-image:url(../../assets/sc_img_chapter.png);
        background-position:-9.95rem -20.325rem;
        background-size:27.1rem 27.1rem;
        background-repeat:no-repeat;
        margin:0 0.85rem ;
    }
    .next_chapter{
        display:block;
        width:1.35rem;
        height:1.35rem;
        background-image:url(../../assets/sc_img_chapter.png);
        background-position:-11.55rem -20.325rem;
        background-size:27.1rem 27.1rem;
        background-repeat:no-repeat;
        margin: 0 0.85rem;

    }
    }
}
.top{
    position:fixed;
    left:0;
    right:0;
    top:0;
    background-color: rgba(40, 40, 40, 0.95);
    display:flex;
    height:2.5rem;
    z-index:2;
}

    span{
        display:block;
        width:1.35rem;
        height:1.35rem;
        background-image: url("../../assets/sc_img_search.png");
        background-repeat: no-repeat;
        background-position:-2.15rem -1.75rem;
        background-size:5.1rem 4.275rem;
        position: relative;
        top:0.6rem;
        left:0.5rem;
    }
     p{
       margin:0 0.5rem;
       color:#969696;
       font-size:0.75rem;
       line-height: 2.5rem;
    }
  .show_top-enter, .show_top-leave-to {
      transform: translate3d(0, -100%, 0);
    }
    .show_top-leave, .show_top-enter-to {
      transform: translate3d(0, 0, 0);
    }
    .show_top-enter-active, .show_top-leave-active {
      transition: all .3s ease;
    }
    .show_footer-enter, .show_footer-leave-to {
      transform: translate3d(0, 100%, 0);
    }
    .show_footer-leave, .show_footer-enter-to {
      transform: translate3d(0, 0, 0);
    }
    .show_footer-enter-active, .show_footer-leave-active {
      transition: all .3s ease;
    }
    .tip{
        font-size:0.65rem;
        line-height:0rem;
        text-align: center;
        color:#969696;
        padding-bottom:1rem;
        margin-top:0.5rem;
    }
</style>